	onHover() {
		if (!this.isChangingRandom) {
			this.isChangingRandom = true;
			this.intervalId = setInterval(() => {
				this.filterClients();
			}, 2000);
		}
	}

	filterClients() {
		this.filteredClients.forEach((cl) => {
			cl.class = '';
		});
		let notvisibleClients = this.clientImgarray;
		let randomClients = [];
		for (var i = 0; i < 2; i++) {
			var idx = Math.floor(Math.random() * notvisibleClients.length);
			let tempRandom = randomClients.filter((cl) => cl.id == idx);
			if (tempRandom.length == 0) {
				randomClients.push(notvisibleClients[idx]);
			}
		}
		for (var j = 0; j < randomClients.length; j++) {
			let tempRandom = this.filteredClients.filter(
				(cl) => cl.id == randomClients[j].id
			);
			if (tempRandom.length == 0) {
				var idy = Math.floor(Math.random() * this.filteredClients.length);
				this.filteredClients[idy] = randomClients[j];
				this.filteredClients[idy].class = 'blur';
			}
		}
	}

	isWeekend(date: NgbDateStruct) {
		const d = new Date(date.year, date.month - 1, date.day);
		return d.getDay() === 0 || d.getDay() === 6;
	}

	isDisabled(date: NgbDateStruct, current: { month: number }) {
		return date.month !== current.month;
	}

