	get_PageMeta() {
		let obj = { pageName: 'home' };
		this.pageservice.getpageWithName(obj).subscribe((response) => {
			if (response.code == 200) {
				this.titleService.setTitle(response?.body.result.meta_title);
				this.metaTagService.updateTag({
					name: 'description',
					content: response?.body.result.meta_description,
				});
				this.metaTagService.updateTag({
					name: 'keywords',
					content: response?.body.result.meta_keywords,
				});
				this.metaTagService.updateTag({
					rel: 'canonical',
					href: 'https://www.pixtar.ae/home',
				});
			} else if (response.code == 400) {
			} else {
			}
		});
	}

	fetch() {
		const api = `https://jsonplaceholder.typicode.com/albums/1/photos?_start=0&_limit=${this.limit}`;
		const http$ = this.http.get<PhotosApi>(api);

		http$.subscribe(
			(res) => (this.apiData = res),
			(err) => throwError(err)
		);
	}

	get_AllProjects() {
		const obj = {
			limit: this.currentLimit,
			page: this.currentPage,
		};
		this.projectservice.getAllProjects(obj).subscribe((response) => {
			if (response.code == 200) {
				if (response.result && response.result.length > 0) {
					response.result.forEach((project) => {
						if (project.status) {
							this.allprojects.push(project);
						}
					});
				}
			} else if (response.code == 400) {
			} else {
			}
		});
	}

	loadMore() {
		this.currentLimit = this.currentLimit + 4;
		this.get_AllProjects();
	}

